body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  width: 40%;
  margin: 0 auto;
}

.sortingElements{
  display:flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.itemsLeft{
  text-align:left;
}
.itemsLeft button{
  font-size: 14px;
  padding: 5px 45px;
  background-color: rgb(76, 172, 240);
  border-bottom: 1px solid rgb(45, 160, 140);
  color: white;
  border-radius:5%;
  cursor: pointer;
  margin-right:5px;
  outline: none;
  text-decoration: none;
  
  
}

.itemsRight{
  text-align: right;
}
.petInfo{
    border: 2px solid black;
    display: flex;
    width: 100%;
    margin-bottom:5px;
    position: relative;
}

.petInfo img{
    height: 200px;
    margin: 2px 20px 2px 2px;
}

.petInfo p{
    text-align: left;
    font-size: 1.1rem;
    margin: 6px;

}

.close{
    position: absolute;
    top: -75px;
    right: -110px;
    -webkit-transform:scale(0.2);
            transform:scale(0.2);
    cursor: pointer;
    
}

.favFoodContainer{
    margin-top: 25px;
    text-align: left;
    margin-bottom: 5px;
}

p.dish{
    font-size: .9rem;
    display: inline;
    border: 2px solid black;
    margin: 2px;
    padding: 1px;
    line-height: 200%;
 
}


